@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
body {
  --main-color: #00acdc;
  --white-color: #fff;
  --black-color: #000;
  box-sizing: border-box;
  background: var(--white-color);
  color: var(--black-color);
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.logo {
  width: 40px;
  height: 40px;
}

.navbar {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  flex-direction: row;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: var(--black-color);
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  display: flex;
}

.nav__link:hover {
  color: var(--main-color);
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

